class ProductosController {
    constructor() {
        this.filtros = document.querySelectorAll('.filtro-btn');
        this.productos = document.querySelectorAll('.producto-card');
        this.btnVerMas = document.getElementById('btnVerMasProductos');
        
        // Estado
        this.filtroActual = 'todos';
        this.mostrandoTodos = false;
        this.productosPorPagina = 4;
        
        this.init();
    }

    init() {
        if (this.productos.length === 0) {
            console.warn('Productos: No se encontraron productos');
            return;
        }

        this.setupFilters();
        this.setupVerMasButton();
        this.setupAnimations();
        this.setupHoverEffects();
        this.setupCotizaciones();
        
        console.log(' Productos Controller inicializado');
    }

    // ==================
    // SISTEMA DE FILTROS
    // ==================
    setupFilters() {
        if (this.filtros.length === 0) return;

        this.filtros.forEach(filtro => {
            filtro.addEventListener('click', () => {
                this.cambiarFiltro(filtro);
            });
        });

        // Mostrar estado inicial
        this.actualizarBotonVerMas();
    }

    cambiarFiltro(filtroClickeado) {
        // Actualizar filtro activo
        this.filtros.forEach(f => f.classList.remove('activo'));
        filtroClickeado.classList.add('activo');
        
        const categoria = filtroClickeado.dataset.filtro;
        this.filtroActual = categoria;
        
        // Resetear estado de "Ver ms"
        this.mostrandoTodos = false;
        
        // Aplicar filtro
        this.aplicarFiltro(categoria);
        
        // Actualizar botn "Ver ms"
        this.actualizarBotonVerMas();
        
        // Tracking
        BombasAyalaUtils.trackEvent('filter_change', 'productos', categoria);
        
        console.log(` Filtro aplicado: ${categoria}`);
    }

    aplicarFiltro(categoria) {
        this.productos.forEach(producto => {
            const categoriaProducto = producto.getAttribute('data-categoria');
            const esVisible = categoria === 'todos' || categoriaProducto === categoria;
            const esOculto = producto.classList.contains('producto-oculto');
            
            if (esVisible) {
                // Mostrar si debe ser visible y no est marcado como oculto para "Ver ms"
                if (!esOculto || this.mostrandoTodos) {
                    this.mostrarProducto(producto);
                } else {
                    this.ocultarProducto(producto);
                }
            } else {
                this.ocultarProducto(producto);
            }
        });
    }

    mostrarProducto(producto) {
        producto.style.display = 'block';
        producto.style.animation = 'fadeInUp 0.5s ease forwards';
    }

    ocultarProducto(producto) {
        producto.style.display = 'none';
    }

    // ==================
    // BOTN "VER MS"
    // ==================
    setupVerMasButton() {
        if (!this.btnVerMas) return;

        this.btnVerMas.addEventListener('click', () => {
            this.toggleVerMas();
        });
    }

    toggleVerMas() {
        if (!this.mostrandoTodos) {
            this.mostrarMasProductos();
        } else {
            this.mostrarMenosProductos();
        }
    }

    mostrarMasProductos() {
        this.btnVerMas.innerHTML = '<i class="fas fa-spinner fa-spin"></i> Cargando...';
        
        setTimeout(() => {
            const productosOcultos = this.getProductosOcultos();
            const productosAMostrar = productosOcultos.slice(0, this.productosPorPagina);
            
            productosAMostrar.forEach(producto => {
                this.mostrarProducto(producto);
                producto.classList.remove('producto-oculto');
            });
            
            this.mostrandoTodos = productosOcultos.length <= this.productosPorPagina;
            this.actualizarBotonVerMas();
            
            // Tracking
            BombasAyalaUtils.trackEvent('ver_mas_click', 'productos', 'mostrar_mas');
            
        }, 1000);
    }

    mostrarMenosProductos() {
        // Volver a ocultar productos extras
        this.productos.forEach(producto => {
            if (producto.classList.contains('producto-inicial')) {
                // Mantener productos iniciales
                return;
            }
            
            const categoriaProducto = producto.getAttribute('data-categoria');
            const esVisible = this.filtroActual === 'todos' || categoriaProducto === this.filtroActual;
            
            if (esVisible) {
                producto.classList.add('producto-oculto');
                this.ocultarProducto(producto);
            }
        });
        
        this.mostrandoTodos = false;
        this.actualizarBotonVerMas();
        
        // Tracking
        BombasAyalaUtils.trackEvent('ver_mas_click', 'productos', 'mostrar_menos');
    }

    getProductosOcultos() {
        return Array.from(this.productos).filter(producto => {
            const categoriaProducto = producto.getAttribute('data-categoria');
            const esVisible = this.filtroActual === 'todos' || categoriaProducto === this.filtroActual;
            const esOculto = producto.classList.contains('producto-oculto');
            
            return esVisible && esOculto;
        });
    }

    actualizarBotonVerMas() {
        if (!this.btnVerMas) return;

        const productosOcultos = this.getProductosOcultos();
        
        if (productosOcultos.length > 0 && !this.mostrandoTodos) {
            this.btnVerMas.style.display = 'inline-block';
            this.btnVerMas.innerHTML = '<i class="fas fa-plus-circle"></i> Ver Ms Productos';
        } else if (this.mostrandoTodos) {
            this.btnVerMas.innerHTML = '<i class="fas fa-minus-circle"></i> Ver Menos';
            this.btnVerMas.style.display = 'inline-block';
        } else {
            this.btnVerMas.style.display = 'none';
        }
    }

    // ==================
    // ANIMACIONES
    // ==================
    setupAnimations() {
        // Animar productos al aparecer en viewport
        BombasAyalaUtils.animateOnScroll('.producto-card, .producto-destacado');
        
        // Observador especfico para productos
        const observer = BombasAyalaUtils.createAnimationObserver((entries) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    entry.target.style.animation = 'fadeInUp 0.6s ease forwards';
                }
            });
        });

        this.productos.forEach(producto => observer.observe(producto));
    }

    // ==================
    // EFECTOS HOVER
    // ==================
    setupHoverEffects() {
        this.productos.forEach(card => {
            card.addEventListener('mouseenter', () => {
                card.style.transform = 'translateY(-10px) scale(1.02)';
            });
            
            card.addEventListener('mouseleave', () => {
                card.style.transform = 'translateY(0) scale(1)';
            });
        });
    }

    // ==================
    // COTIZACIONES
    // ==================
    setupCotizaciones() {
        const botonesCotizar = document.querySelectorAll('.boton-cotizar, .boton-producto-cotizar');
        
        botonesCotizar.forEach(boton => {
            boton.addEventListener('click', () => {
                const producto = boton.dataset.producto || 'Producto no especificado';
                const specs = boton.dataset.especificaciones || '';
                
                const datos = {
                    producto: producto,
                    descripcion: specs
                };
                
                const mensaje = BombasAyalaUtils.createQuoteMessage(datos);
                BombasAyalaUtils.openWhatsApp(mensaje);
                
                // Tracking
                BombasAyalaUtils.trackEvent('cotizacion_producto', 'productos', producto);
                
                console.log(' Cotizacin solicitada:', datos);
            });
        });
    }

    // ==================
    // API PBLICA
    // ==================
    filtrarPor(categoria) {
        const filtro = document.querySelector(`[data-filtro="${categoria}"]`);
        if (filtro) {
            this.cambiarFiltro(filtro);
        }
    }

    buscarProducto(termino) {
        termino = termino.toLowerCase();
        
        this.productos.forEach(producto => {
            const titulo = producto.querySelector('.producto-titulo')?.textContent.toLowerCase() || '';
            const descripcion = producto.querySelector('.producto-descripcion')?.textContent.toLowerCase() || '';
            
            const coincide = titulo.includes(termino) || descripcion.includes(termino);
            
            if (coincide) {
                this.mostrarProducto(producto);
            } else {
                this.ocultarProducto(producto);
            }
        });
        
        // Tracking
        BombasAyalaUtils.trackEvent('producto_search', 'productos', termino);
    }

    resetearFiltros() {
        this.filtrarPor('todos');
        this.mostrandoTodos = false;
        this.actualizarBotonVerMas();
    }

    getStats() {
        const totalProductos = this.productos.length;
        const productosVisibles = Array.from(this.productos).filter(p => p.style.display !== 'none').length;
        const productosOcultos = this.getProductosOcultos().length;
        
        return {
            total: totalProductos,
            visibles: productosVisibles,
            ocultos: productosOcultos,
            filtroActual: this.filtroActual,
            mostrandoTodos: this.mostrandoTodos
        };
    }
}

// ==================
// FUNCIONES LEGACY (compatibilidad)
// ==================

function animarElementos() {
    console.log(' animarElementos legacy llamada');
}

function efectosHover() {
    console.log(' efectosHover legacy llamada');
}

// ==================
// INICIALIZACIN
// ==================
function initProductos() {
    // Solo inicializar en pgina de productos
    if (document.querySelector('.producto-card, .productos-grid')) {
        window.productosController = new ProductosController();
        
        // API global
        window.Productos = {
            filtrar: (categoria) => window.productosController?.filtrarPor(categoria),
            buscar: (termino) => window.productosController?.buscarProducto(termino),
            resetear: () => window.productosController?.resetearFiltros(),
            stats: () => window.productosController?.getStats()
        };
    }
}

// Auto-inicializar
document.addEventListener('DOMContentLoaded', () => {
    initProductos();
    
    // Llamar funciones legacy para compatibilidad
    animarElementos();
    efectosHover();
});

// Exportar para mdulos
if (typeof module !== 'undefined' && module.exports) {
    module.exports = ProductosController;
}