class ServiciosController {
    constructor() {
        this.formularioCotizacion = document.getElementById('formularioCotizacion');
        this.servicioCards = document.querySelectorAll('.servicio-card');
        this.testimonioCards = document.querySelectorAll('.testimonio-card');
        this.statsItems = document.querySelectorAll('.stat-item');
        
        this.init();
    }

    init() {
        this.initFormularioCotizacion();
        this.initAnimaciones();
        this.initContadores();
        this.initBotonesCotizar();
        this.initBotonesEmergencia();
        this.initAccesibilidad();
        
        console.log(' Servicios Controller inicializado');
    }

    // ==================
    // FORMULARIO DE COTIZACIN
    // ==================
    initFormularioCotizacion() {
        if (!this.formularioCotizacion) return;

        // Validacin en tiempo real
        const inputs = this.formularioCotizacion.querySelectorAll('input, select, textarea');
        
        inputs.forEach(input => {
            input.addEventListener('blur', () => {
                BombasAyalaUtils.validateField(input);
            });

            input.addEventListener('input', () => {
                if (input.classList.contains('error')) {
                    BombasAyalaUtils.validateField(input);
                }
            });
        });

        // Submit del formulario
        this.formularioCotizacion.addEventListener('submit', (e) => {
            e.preventDefault();
            this.enviarCotizacion();
        });
    }

    enviarCotizacion() {
        // Validar formulario
        if (!BombasAyalaUtils.validateForm(this.formularioCotizacion)) {
            BombasAyalaUtils.showToast('Por favor, corrige los errores en el formulario.', 'error');
            
            // Scroll al primer error
            const primerError = this.formularioCotizacion.querySelector('.error');
            if (primerError) {
                primerError.focus();
                primerError.scrollIntoView({ behavior: 'smooth', block: 'center' });
            }
            return;
        }

        // Obtener datos del formulario
        const formData = new FormData(this.formularioCotizacion);
        const datos = {
            nombre: formData.get('nombre'),
            telefono: formData.get('telefono'),
            email: formData.get('email'),
            servicio: formData.get('servicio'),
            descripcion: formData.get('descripcion')
        };

        // Validar campos requeridos
        if (!datos.nombre || !datos.telefono || !datos.servicio) {
            BombasAyalaUtils.showToast('Por favor, completa todos los campos obligatorios.', 'error');
            return;
        }

        // Mostrar estado de carga
        const botonSubmit = this.formularioCotizacion.querySelector('.boton-enviar, button[type="submit"]');
        const textoOriginal = botonSubmit ? botonSubmit.innerHTML : '';
        
        if (botonSubmit) {
            botonSubmit.innerHTML = '<i class="fas fa-spinner fa-spin"></i> ENVIANDO...';
            botonSubmit.disabled = true;
        }

        // Crear mensaje para WhatsApp
        const mensaje = BombasAyalaUtils.createQuoteMessage(datos);
        
        setTimeout(() => {
            // Abrir WhatsApp
            BombasAyalaUtils.openWhatsApp(mensaje);
            
            // Mostrar mensaje de xito
            BombasAyalaUtils.showToast('Cotizacin enviada! Te hemos redirigido a WhatsApp.', 'success');
            
            // Limpiar formulario
            this.formularioCotizacion.reset();
            const inputs = this.formularioCotizacion.querySelectorAll('input, select, textarea');
            inputs.forEach(input => {
                input.classList.remove('error', 'success');
                BombasAyalaUtils.removeFieldError(input);
            });
            
            // Restaurar botn
            if (botonSubmit) {
                botonSubmit.innerHTML = textoOriginal;
                botonSubmit.disabled = false;
            }
            
            // Tracking
            BombasAyalaUtils.trackEvent('cotizacion_enviada', 'servicios', datos.servicio);
            
            console.log(' Cotizacin enviada:', datos);
            
        }, 1500);
    }

    // ==================
    // ANIMACIONES
    // ==================
    initAnimaciones() {
        // Animar cards de servicios y testimonios
        BombasAyalaUtils.animateOnScroll('.servicio-card, .testimonio-card');
        
        // Configurar transiciones iniciales para cards
        this.servicioCards.forEach(card => {
            card.style.opacity = '0';
            card.style.transform = 'translateY(30px)';
            card.style.transition = 'opacity 0.6s ease, transform 0.6s ease';
        });

        this.testimonioCards.forEach(card => {
            card.style.opacity = '0';
            card.style.transform = 'translateY(30px)';
            card.style.transition = 'opacity 0.6s ease, transform 0.6s ease';
        });
        
        // Observador para animaciones personalizadas
        const observer = BombasAyalaUtils.createAnimationObserver((entries) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    entry.target.style.opacity = '1';
                    entry.target.style.transform = 'translateY(0)';
                }
            });
        });

        // Observar elementos
        [...this.servicioCards, ...this.testimonioCards].forEach(el => {
            observer.observe(el);
        });
    }

    // ==================
    // CONTADORES ANIMADOS
    // ==================
    initContadores() {
        if (this.statsItems.length === 0) return;

        const statsObserver = BombasAyalaUtils.createAnimationObserver((entries) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    const numero = entry.target.querySelector('.stat-numero');
                    if (!numero) return;
                    
                    const texto = numero.textContent;
                    
                    if (texto.includes('5000+')) {
                        BombasAyalaUtils.animateCounter(numero, 0, 5000, 2000);
                    } else if (texto.includes('98%')) {
                        BombasAyalaUtils.animateCounter(numero, 0, 98, 2000);
                    } else if (texto.includes('24/7')) {
                        numero.textContent = '24/7';
                    } else {
                        // Extraer nmero si existe
                        const match = texto.match(/(\d+)/);
                        if (match) {
                            const targetNumber = parseInt(match[1]);
                            BombasAyalaUtils.animateCounter(numero, 0, targetNumber, 2000);
                        }
                    }
                    
                    statsObserver.unobserve(entry.target);
                }
            });
        }, { threshold: 0.5 });

        this.statsItems.forEach(item => {
            statsObserver.observe(item);
        });
    }

    // ==================
    // BOTONES DE COTIZACIN
    // ==================
    initBotonesCotizar() {
        const botonesCotizar = document.querySelectorAll('.boton-cotizar');
        
        botonesCotizar.forEach(boton => {
            boton.addEventListener('click', () => {
                const servicio = this.getServicioFromButton(boton);
                
                // Scroll al formulario
                if (this.formularioCotizacion) {
                    // Pre-seleccionar servicio si existe el campo
                    const selectServicio = this.formularioCotizacion.querySelector('select[name="servicio"]');
                    if (selectServicio && servicio) {
                        selectServicio.value = servicio;
                    }
                    
                    // Scroll suave al formulario
                    this.formularioCotizacion.scrollIntoView({
                        behavior: 'smooth',
                        block: 'start'
                    });
                    
                    // Focus en el primer campo
                    setTimeout(() => {
                        const primerInput = this.formularioCotizacion.querySelector('input');
                        if (primerInput) primerInput.focus();
                    }, 800);
                }
                
                // Tracking
                BombasAyalaUtils.trackEvent('boton_cotizar_click', 'servicios', servicio);
                
                console.log(' Botn cotizar clickeado:', servicio);
            });
        });
    }

    getServicioFromButton(boton) {
        // Intentar obtener el servicio desde varios lugares
        const servicioCard = boton.closest('.servicio-card');
        
        if (servicioCard) {
            const titulo = servicioCard.querySelector('.servicio-titulo, h3, h4');
            if (titulo) return titulo.textContent.trim();
        }
        
        // Fallback: usar data attribute
        return boton.dataset.servicio || 'Servicio no especificado';
    }

    // ==================
    // BOTONES DE EMERGENCIA
    // ==================
    initBotonesEmergencia() {
        const botonesEmergencia = document.querySelectorAll('.boton-emergencia');
        
        botonesEmergencia.forEach(boton => {
            boton.addEventListener('click', () => {
                const tipo = this.getTipoEmergencia(boton);
                
                if (tipo === 'whatsapp') {
                    const mensaje = ' *EMERGENCIA - SERVICIO URGENTE* \n\n' +
                                  'Necesito asistencia tcnica urgente con bombas de agua.\n\n' +
                                  ' Es una emergencia\n' +
                                  ' Por favor contactarme lo antes posible\n\n' +
                                  'Gracias!';
                    
                    BombasAyalaUtils.openWhatsApp(mensaje);
                } else if (tipo === 'llamada') {
                    window.location.href = 'tel:+51997984102';
                }
                
                // Tracking
                BombasAyalaUtils.trackEvent('emergencia_click', 'servicios', tipo);
                
                console.log(' Emergencia clickeada:', tipo);
            });
        });
    }

    getTipoEmergencia(boton) {
        if (boton.classList.contains('whatsapp') || boton.querySelector('.fa-whatsapp')) {
            return 'whatsapp';
        } else if (boton.classList.contains('llamada') || boton.querySelector('.fa-phone')) {
            return 'llamada';
        }
        return 'unknown';
    }

    // ==================
    // MEJORAS DE ACCESIBILIDAD
    // ==================
    initAccesibilidad() {
        // Mejorar accesibilidad de botones sin texto
        const botones = document.querySelectorAll('button, .boton-cotizar, .boton-emergencia');
        
        botones.forEach(boton => {
            if (!boton.getAttribute('aria-label') && !boton.textContent.trim()) {
                const icono = boton.querySelector('i');
                if (icono) {
                    const clases = icono.className;
                    
                    if (clases.includes('whatsapp')) {
                        boton.setAttribute('aria-label', 'Contactar por WhatsApp');
                    } else if (clases.includes('phone')) {
                        boton.setAttribute('aria-label', 'Llamar por telfono');
                    } else if (clases.includes('quote') || clases.includes('cotizar')) {
                        boton.setAttribute('aria-label', 'Solicitar cotizacin');
                    }
                }
            }
        });

        // Mejorar contraste de foco
        const elementos = document.querySelectorAll('input, select, textarea, button');
        elementos.forEach(el => {
            el.addEventListener('focus', () => {
                el.style.outline = '2px solid #007bff';
                el.style.outlineOffset = '2px';
            });
            
            el.addEventListener('blur', () => {
                el.style.outline = '';
                el.style.outlineOffset = '';
            });
        });
    }

    // ==================
    // API PBLICA
    // ==================
    scrollToForm() {
        if (this.formularioCotizacion) {
            this.formularioCotizacion.scrollIntoView({
                behavior: 'smooth',
                block: 'start'
            });
        }
    }

    preseleccionarServicio(servicio) {
        if (this.formularioCotizacion) {
            const selectServicio = this.formularioCotizacion.querySelector('select[name="servicio"]');
            if (selectServicio) {
                selectServicio.value = servicio;
                selectServicio.dispatchEvent(new Event('change'));
            }
        }
    }

    getStats() {
        return {
            servicios: this.servicioCards.length,
            testimonios: this.testimonioCards.length,
            hasFormulario: !!this.formularioCotizacion
        };
    }
}

// ==================
// FUNCIONES LEGACY (compatibilidad)
// ==================

function trackEvent(action, category, label) {
    BombasAyalaUtils.trackEvent(action, category, label);
}

function validateField(field) {
    return BombasAyalaUtils.validateField(field);
}

function showFieldError(field, message) {
    BombasAyalaUtils.showFieldError(field, message);
}

function animateCounter(element, start, end, duration) {
    BombasAyalaUtils.animateCounter(element, start, end, duration);
}

function isMobile() {
    return BombasAyalaUtils.isMobile();
}

function adjustForMobile() {
    console.log(' adjustForMobile legacy llamada');
}

// ==================
// INICIALIZACIN
// ==================
function initServicios() {
    window.serviciosController = new ServiciosController();
    
    // API global
    window.Servicios = {
        scrollToForm: () => window.serviciosController?.scrollToForm(),
        preseleccionar: (servicio) => window.serviciosController?.preseleccionarServicio(servicio),
        stats: () => window.serviciosController?.getStats()
    };
}

// Auto-inicializar
document.addEventListener('DOMContentLoaded', () => {
    initServicios();
    
    // Mantener compatibilidad con funciones legacy
    adjustForMobile();
});

// Exportar para mdulos
if (typeof module !== 'undefined' && module.exports) {
    module.exports = ServiciosController;
}