class NosotrosController {
    constructor() {
        // Elementos principales
        this.contadores = document.querySelectorAll('.numero-estadistica');
        this.overlayVideo = document.querySelector('.overlay-imagen');
        this.iconoPlay = document.querySelector('.icono-play');
        this.listaDistritos = document.querySelector('.lista-distritos');
        
        // Datos de Lima Metropolitana (4 zonas)
        this.zonas = {
            norte: {
                distritos: [
                    'Ancn', 'Carabayllo', 'Comas', 'Independencia', 
                    'Los Olivos', 'Puente Piedra', 'San Martn de Porres', 'Santa Rosa'
                ],
                color: 'var(--cian-principal)',
                descripcion: 'Lima Norte - 8 distritos'
            },
            sur: {
                distritos: [
                    'Chorrillos', 'Lurn', 'Pachacmac', 'Pucusana', 
                    'San Juan de Miraflores', 'Villa El Salvador', 'Villa Mara del Triunfo'
                ],
                color: 'var(--verde-esmeralda)',
                descripcion: 'Lima Sur - 7 distritos'
            },
            este: {
                distritos: [
                    'Ate', 'Chaclacayo', 'Cieneguilla', 'El Agustino', 
                    'La Molina', 'Lurigancho-Chosica', 'San Juan de Lurigancho', 'Santa Anita'
                ],
                color: 'var(--naranja-tecnico)',
                descripcion: 'Lima Este - 8 distritos'
            },
            oeste: {
                distritos: [
                    'Barranco', 'Brea', 'Callao', 'Jess Mara', 'La Victoria', 
                    'Lima Cercado', 'Lince', 'Magdalena del Mar', 'Miraflores', 
                    'Pueblo Libre', 'Rmac', 'San Borja', 'San Isidro', 'San Luis', 
                    'San Miguel', 'Santiago de Surco', 'Surquillo'
                ],
                color: 'var(--azul-claro)',
                descripcion: 'Lima Oeste - 17 distritos'
            }
        };

        // Estadsticas de la empresa
        this.estadisticas = {
            experiencia: 25,
            clientes: 5000,
            servicios: 15000,
            atencion: 24
        };

        this.init();
    }

    init() {
        this.initContadoresAnimados();
        this.initMapaInteractivo();
        this.initVideoOverlay();
        this.initAnimaciones();
        this.initInformacionInicial();
        this.initBotonesCTA();
        
        console.log(' Nosotros Controller inicializado');
    }

    // ==================
    // CONTADORES ANIMADOS
    // ==================
    initContadoresAnimados() {
        if (this.contadores.length === 0) return;

        const observerContadores = BombasAyalaUtils.createAnimationObserver((entries) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    const elemento = entry.target;
                    const objetivo = parseInt(elemento.getAttribute('data-numero'));
                    
                    if (objetivo) {
                        BombasAyalaUtils.animateCounter(elemento, 0, objetivo, 2000);
                        observerContadores.unobserve(elemento);
                        
                        console.log(` Contador animado: ${objetivo}`);
                    }
                }
            });
        }, { threshold: 0.5 });

        this.contadores.forEach(contador => {
            observerContadores.observe(contador);
        });
    }

    // ==================
    // MAPA INTERACTIVO DE LIMA (4 ZONAS)
    // ==================
    initMapaInteractivo() {
        const zonasElementos = document.querySelectorAll('.zona-norte, .zona-sur, .zona-este, .zona-oeste');
        
        if (zonasElementos.length === 0) return;

        zonasElementos.forEach(zona => {
            zona.addEventListener('mouseenter', () => {
                const tipoZona = this.getTipoZona(zona);
                this.mostrarDistritos(tipoZona);
            });
            
            // Efectos hover
            zona.addEventListener('mouseover', () => {
                zona.style.transform = 'scale(1.05)';
                zona.style.zIndex = '10';
                zona.style.transition = 'all 0.3s ease';
            });
            
            zona.addEventListener('mouseout', () => {
                zona.style.transform = 'scale(1)';
                zona.style.zIndex = '1';
            });

            // Click para ms informacin
            zona.addEventListener('click', () => {
                const tipoZona = this.getTipoZona(zona);
                this.mostrarInformacionZona(tipoZona);
            });
        });
    }

    getTipoZona(elemento) {
        if (elemento.classList.contains('zona-norte')) return 'norte';
        if (elemento.classList.contains('zona-sur')) return 'sur';
        if (elemento.classList.contains('zona-este')) return 'este';
        return 'oeste';
    }

    mostrarDistritos(tipoZona) {
        if (!this.listaDistritos) return;

        const zona = this.zonas[tipoZona];
        if (!zona) return;

        this.listaDistritos.innerHTML = `
            <div class="zona-titulo" style="color: ${zona.color}; font-weight: 700;">
                <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
                    <circle cx="12" cy="10" r="3"/>
                    <path d="M12 21.7C17.3 17 20 13 20 10a8 8 0 1 0-16 0c0 3 2.7 7 8 11.7z"/>
                </svg>
                ${zona.descripcion}
            </div>
            <div class="distritos-grid">
                ${zona.distritos.map(distrito => `
                    <div class="distrito-item" style="color: ${zona.color}; font-weight: 600;">
                        <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
                            <circle cx="12" cy="10" r="3"/>
                            <path d="M12 21.7C17.3 17 20 13 20 10a8 8 0 1 0-16 0c0 3 2.7 7 8 11.7z"/>
                        </svg>
                        ${distrito}
                    </div>
                `).join('')}
            </div>
        `;

        // Animar entrada de distritos
        const items = this.listaDistritos.querySelectorAll('.distrito-item');
        items.forEach((item, index) => {
            item.style.opacity = '0';
            item.style.transform = 'translateX(-20px)';
            
            setTimeout(() => {
                item.style.transition = 'all 0.3s ease';
                item.style.opacity = '1';
                item.style.transform = 'translateX(0)';
            }, index * 50);
        });

        // Tracking
        BombasAyalaUtils.trackEvent('mapa_hover', 'nosotros', tipoZona);
    }

    mostrarInformacionZona(tipoZona) {
        const zona = this.zonas[tipoZona];
        if (!zona) return;

        const mensaje = ` **${zona.descripcion}**\n\n` +
                       ` **Distritos atendidos:** ${zona.distritos.length}\n\n` +
                       ` **Lista completa:**\n${zona.distritos.join(', ')}\n\n` +
                       ` **Servicios disponibles:**\n` +
                       ` Instalacin de bombas\n` +
                       ` Mantenimiento preventivo\n` +
                       ` Reparaciones de emergencia\n` +
                       ` Asesora tcnica\n\n` +
                       ` **Necesitas servicio en esta zona?**`;

        BombasAyalaUtils.openWhatsApp(mensaje);

        // Tracking
        BombasAyalaUtils.trackEvent('zona_click', 'nosotros', tipoZona);
    }

    // ==================
    // VIDEO OVERLAY
    // ==================
    initVideoOverlay() {
        if (!this.overlayVideo || !this.iconoPlay) return;

        this.overlayVideo.addEventListener('click', () => {
            // Efecto en el icono
            this.iconoPlay.style.transform = 'scale(1.2)';
            setTimeout(() => {
                this.iconoPlay.style.transform = 'scale(1)';
            }, 200);

            // Aqu puedes integrar con tu reproductor de video preferido
            this.mostrarModalVideo();

            // Tracking
            BombasAyalaUtils.trackEvent('video_play', 'nosotros', 'video_empresarial');
        });
    }

    mostrarModalVideo() {
        // Placeholder para modal de video
        const modal = document.createElement('div');
        modal.style.cssText = `
            position: fixed;
            top: 0;
            left: 0;
            width: 100vw;
            height: 100vh;
            background: rgba(0,0,0,0.9);
            display: flex;
            justify-content: center;
            align-items: center;
            z-index: 9999;
        `;

        modal.innerHTML = `
            <div style="text-align: center; color: white; max-width: 500px; padding: 20px;">
                <h3> Video de Presentacin</h3>
                <p>Aqu se mostrara el video institucional de Bombas Ayala</p>
                <br>
                <button onclick="this.closest('div').parentNode.remove()" 
                        style="background: #007bff; color: white; border: none; padding: 10px 20px; border-radius: 5px; cursor: pointer;">
                    Cerrar
                </button>
            </div>
        `;

        document.body.appendChild(modal);

        // Cerrar con click fuera del contenido
        modal.addEventListener('click', (e) => {
            if (e.target === modal) {
                document.body.removeChild(modal);
            }
        });
    }

    // ==================
    // ANIMACIONES SCROLL
    // ==================
    initAnimaciones() {
        // Animar elementos principales
        BombasAyalaUtils.animateOnScroll('.estadistica-item, .mision-section, .vision-section, .valor-item');

        // Observador especfico para elementos de nosotros
        const observer = BombasAyalaUtils.createAnimationObserver((entries) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    entry.target.style.opacity = '1';
                    entry.target.style.transform = 'translateY(0)';
                    entry.target.style.transition = 'all 0.6s ease-out';
                    observer.unobserve(entry.target);
                }
            });
        });

        // Configurar elementos para animacin
        const elementosAnimados = document.querySelectorAll('.estadistica-item, .mision-section, .vision-section, .valor-item');
        
        elementosAnimados.forEach(elemento => {
            elemento.style.opacity = '0';
            elemento.style.transform = 'translateY(30px)';
            observer.observe(elemento);
        });
    }

    // ==================
    // INFORMACIN INICIAL
    // ==================
    initInformacionInicial() {
        if (!this.listaDistritos || this.listaDistritos.innerHTML.trim() !== '') return;

        const datosLima = this.getDatosLima();

        this.listaDistritos.innerHTML = `
            <div class="zona-titulo" style="color: var(--azul-corporativo); font-weight: 700;">
                <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
                    <circle cx="12" cy="10" r="3"/>
                    <path d="M12 21.7C17.3 17 20 13 20 10a8 8 0 1 0-16 0c0 3 2.7 7 8 11.7z"/>
                </svg>
                Cobertura en Lima Metropolitana (4 Zonas)
            </div>
            <div class="distritos-grid">
                <div class="distrito-item" style="color: var(--gris-medio);">
                    <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
                        <circle cx="12" cy="12" r="10"/>
                        <line x1="12" y1="16" x2="12" y2="12"/>
                        <line x1="12" y1="8" x2="12.01" y2="8"/>
                    </svg>
                    Pasa el cursor sobre una zona para ver los distritos
                </div>
                <div class="distrito-item" style="color: var(--azul-corporativo);">
                    <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
                        <circle cx="12" cy="10" r="3"/>
                        <path d="M12 21.7C17.3 17 20 13 20 10a8 8 0 1 0-16 0c0 3 2.7 7 8 11.7z"/>
                    </svg>
                    ${datosLima.totalDistritos} distritos en 4 zonas
                </div>
                <div class="distrito-item" style="color: var(--verde-esmeralda);">
                    <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
                        <circle cx="12" cy="12" r="10"/>
                        <polyline points="12,6 12,12 16,14"/>
                    </svg>
                    Tiempo respuesta: ${datosLima.tiempoRespuestaPromedio}
                </div>
            </div>
        `;
    }

    // ==================
    // BOTONES CTA
    // ==================
    initBotonesCTA() {
        const botonesCTA = document.querySelectorAll('.boton-cta');
        
        botonesCTA.forEach(boton => {
            boton.addEventListener('click', () => {
                const accion = boton.textContent.trim();
                const tipo = boton.classList.contains('primario') ? 'primario' : 'secundario';
                
                // Tracking especfico
                BombasAyalaUtils.trackEvent('cta_click', 'nosotros', accion, tipo);
                
                console.log(` CTA clicked: ${accion} (${tipo})`);
            });
        });
    }

    // ==================
    // FUNCIONES DE UTILIDAD
    // ==================
    getDatosLima() {
        const totalDistritos = Object.values(this.zonas).reduce((total, zona) => {
            return total + zona.distritos.length;
        }, 0);

        return {
            totalDistritos,
            poblacionAproximada: '10.5 millones',
            areaCobertura: '100%',
            tiempoRespuestaPromedio: '20 minutos',
            zonasCobertura: 4
        };
    }

    // ==================
    // API PBLICA
    // ==================
    mostrarZona(tipoZona) {
        this.mostrarDistritos(tipoZona);
    }

    actualizarEstadisticas(nuevasEstadisticas) {
        Object.keys(nuevasEstadisticas).forEach(key => {
            if (this.estadisticas[key] !== undefined) {
                this.estadisticas[key] = nuevasEstadisticas[key];
                
                // Actualizar el elemento en el DOM si existe
                const elemento = document.querySelector(`[data-numero="${this.estadisticas[key]}"]`);
                if (elemento) {
                    elemento.setAttribute('data-numero', nuevasEstadisticas[key]);
                }
            }
        });
        
        console.log(' Estadsticas actualizadas:', nuevasEstadisticas);
    }

    getStats() {
        return {
            ...this.estadisticas,
            datosLima: this.getDatosLima(),
            zonas: Object.keys(this.zonas)
        };
    }
}

// ==================
// FUNCIONES LEGACY (compatibilidad)
// ==================

function mostrarDistritos(tipoZona) {
    if (window.nosotrosController) {
        window.nosotrosController.mostrarZona(tipoZona);
    }
}

// ==================
// INICIALIZACIN
// ==================
function initNosotros() {
    // Solo inicializar en pgina de nosotros
    if (document.querySelector('.numero-estadistica, .zona-norte, .mision-section')) {
        window.nosotrosController = new NosotrosController();
        
        // API global
        window.Nosotros = {
            mostrarZona: (zona) => window.nosotrosController?.mostrarZona(zona),
            actualizarStats: (stats) => window.nosotrosController?.actualizarEstadisticas(stats),
            getStats: () => window.nosotrosController?.getStats()
        };

        // Mantener compatibilidad con funcin legacy
        window.BombasAyala = {
            mostrarDistritos: mostrarDistritos,
            actualizarEstadisticas: (stats) => window.nosotrosController?.actualizarEstadisticas(stats),
            datosLima: window.nosotrosController?.getDatosLima(),
            zonas: window.nosotrosController?.zonas
        };
    }
}

// Auto-inicializar
document.addEventListener('DOMContentLoaded', () => {
    initNosotros();
    
    // Prevenir flicker y mostrar contenido
    document.body.style.opacity = '1';
    
    console.log(' Bombas Ayala - Pgina Nosotros iniciada');
    console.log(' Zonas cubiertas: Norte, Sur, Este, Oeste (4 zonas)');
    console.log(' Scripts cargados correctamente');
});

// Exportar para mdulos
if (typeof module !== 'undefined' && module.exports) {
    module.exports = NosotrosController;
}